        position: absolute;
        top: 100px;
        height: auto;
        background: #333;
        font-family: "Anton", sans-serif;
        display: flex;
        align-items: center;
        flex-direction: column;
        justify-content: center;
        overflow: hidden;
        z-index: 10;
        padding: 10px 0px;

    }
    .mobile-menu a{
        text-decoration: none;
        font-size: 20px;
        color: white;
        text-transform: uppercase;
