    height: 0%;
}
.hide-module-btn span:nth-child(4){
    bottom: 0;
    left: 0;
    width: 1px;
    height: 0%;
}
.hide-module-btn:hover span:nth-child(1),
.hide-module-btn:hover span:nth-child(2){
    width: 100%;
}
.hide-module-btn:hover span:nth-child(3),
.hide-module-btn:hover span:nth-child(4){
    height: 100%;
}
.hide-module-btn:hover{
    transform: scale(1.2);
}

