.buy-item{
    width: 90%;
    list-style: none;
    height: auto;
    border: solid 2px rgb(0, 193, 241);
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    position: relative;
    background: #333;
    transition: .3s;
}
.buy-item:hover{
background: rgb(0, 193, 241);
box-shadow: inset 0px 0px 10px #333;
width: 91%;
}
.buy-item img{
    height: auto;
    width: 80px;
    margin: 0px 20px;
    border: solid 2px rgb(0, 193, 241);
}
.buy-item p {
    font-family: "Anton", sans-serif;
    font-size: 20px;
    color: white;
    text-shadow: 1px 1px 1px black;
}
.cena-produktu{
    font-family: "Anton", sans-serif;
    font-size: 20px;
    color: white;
    position: absolute;
    right: 100px;
    text-shadow: 1px 1px 1px black;
}
.remove-btn-buy{
    position: absolute;
    right: 20px;
    border: none;
    font-size: 25px;
    background: transparent;
    cursor: pointer;
    color: white;
    outline: none;
}
.remove-btn-buy:hover{
    color: #333;
}
.zakupit{
    margin-top: 50px;
    width: 90%;
    height: 50px;
    font-size: 30px;
    text-transform: uppercase;
    background: rgb(0, 193, 241);
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
}
.zakupit:hover{
    box-shadow: inset 0px 0px 5px #333;
    border: solid 2px #333;
}
@media screen and (max-width: 500px){
    .buy-item{
        width: 95%;
    }
    .buy-item img{
        height: auto;
        width: 60px;
        margin: 0px 10px;
        border: solid 1px rgb(0, 193, 241);
    }
    .buy-item p {
        font-family: "Anton", sans-serif;
        font-size: 14px;
        color: white;
        text-shadow: 1px 1px 1px black;
    }
    .cena-produktu{
        font-family: "Anton", sans-serif;
        font-size: 14px;
        color: white;
        position: absolute;
        right: 50px;
        text-shadow: 1px 1px 1px black;
    }
    .remove-btn-buy{
        position: absolute;
        right: 10px;
        border: none;
        font-size: 20px;
        background: transparent;
        cursor: pointer;
        color: white;
        outline: none;
    }
}